.tooltip-container {
  display: none; }

.tooltip-block {
  position: relative;
  font-size: 10px;
  border-radius: 10px;
  overflow: hidden;
  max-width: 250px;
  min-width: 200px;
  color: #494949; }
  .tooltip-block__close {
    width: 28px;
    height: 28px;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
    outline: none;
    border: 0;
    background: transparent;
    padding: 0; }
    .tooltip-block__close:before, .tooltip-block__close:after {
      content: '';
      display: block;
      width: 12px;
      height: 2px;
      background: #C4C4C4;
      position: absolute;
      left: 50%;
      top: 50%;
      transition: 0.3s; }
    .tooltip-block__close:before {
      transform: translate(-50%, -50%) rotate(45deg); }
    .tooltip-block__close:after {
      transform: translate(-50%, -50%) rotate(-45deg); }
    .tooltip-block__close:hover:before, .tooltip-block__close:hover:after {
      background-color: #636363; }
  .tooltip-block__photo {
    display: block;
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 100%;
    position: relative;
    flex-shrink: 0;
    background-color: #C4C4C4; }
    .tooltip-block__photo img {
      display: block;
      width: 100%;
      height: auto;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      position: absolute; }
  .tooltip-block__container {
